@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);

body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background: #f8f8f8;
}

@media only screen and (min-width: 768px) {
    .card {
        max-width: 75%;
        margin: 0 auto; /* Added */
        float: none; /* Added */
    }
}

.justify-text {
    text-align: justify;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
    color: darkcyan;
    text-decoration: none;
}

a:hover {
    color: #005e5e;
}

.section-title {
    text-align: left;
    margin: 20px 0;
}

.key-element {
    text-align: left !important;
    margin: 8px 0;
    font-size: 18px;
    font-weight: 500;
}

.text-left {
    text-align: left !important;
}

.pl-1 {
    padding-left: 15px !important;
}

.word-break {
    word-break: break-word;
}

td {
    vertical-align: top;
    padding: 3px 0;
    min-width: 100px;
}

ul {
    padding-left: 0 !important;
}

@media only screen and (min-width: 768px) {
    ul {
        padding-left: 2rem !important;
    }

    .p-l-2 {
        padding-left: 2rem !important;
    }
}

li {
    list-style: none;
}

b {
    font-weight: 500;
}

.text-small {
    font-size: 15px;
}
.card {
    background: #fff;
    border: none;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.profile-name {
    font-weight: 500;
    font-size: 36px;
}

.profile-picture {
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.email-icon {
    font-size: 18px;
    cursor: pointer;
}
.social-card {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(168, 10, 10, 0.3) 0px 30px 60px -30px !important;
}
